                const val = amount.toString();
                return val.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
            },
            getStatusColor: function (eventStatus) {
                let color = '';
                switch (eventStatus) {
                    case 1:
                        color = 'green';
                        break;
                    case 2:
                        color = 'red';
                        break;
                    case 3:
                        color = 'gary';
                        break;
                    case 4:
                        color = 'orange';
                        break;
                    default:
                        color = 'gary';
                        break;
                }
                return color;
            },
            getProcessColor: function (groupIdx) {
                let color = '';
                switch (groupIdx) {
                    case 1:
                        color = 'primary';
                        break;
                    case 2:
                        color = 'orange';
                        break;
                    case 3:
                        color = 'green';
                        break;
                    case 4:
                        color = 'purple';
                        break;
                    case 5:
                        color = 'red';
                        break;
                    default:
                        color = 'gary';
                        break;
                }
                return color;
