	methods: {
		showToDealDialog() {
			this.toDealDialog = true;
		},
		onEmitToDealDialog() {
			this.toDealDialog = false;
		},
		amountFormat: function (amount) {
			const val = amount.toString();
			return val.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
		},
		getStatusColor: function (eventStatus) {
			let color = '';
			switch (eventStatus) {
				case 1:
					color = 'green';
					break;
				case 2:
					color = 'red';
					break;
				case 3:
					color = 'gary';
					break;
				case 4:
					color = 'orange';
					break;
				default:
					color = 'gary';
					break;
			}
			return color;
		},
		onCall: function (cell) {
            return 'tel:'+cell;
        },
        onMall: function (mail) {
            return 'mailto:'+mail;
        },
	},
