<template lang='pug'>
	#Deal.main-page.page-noScroll.detail-page
		v-overlay(v-model='loading' z-index='99' opacity='.6')
			v-progress-circular(indeterminate color='primary')
		div.height_100(v-show='!loading')
			v-breadcrumbs.pa-0.px-3.pb-2(:items='breadcrumbs')
			v-row#page-head.d-flex.align-center.my-0
				v-col(cols='12' lg='7')
					h1.my-0.d-flex.align-center
						.cube-icon.bg-og.mr-3
							v-icon(color='white' size='22') icon-chatmoney-border
						span {{ deal.dealName }}
				v-col(cols='12' lg='5')
					v-row.justify-md-end(no-gutters='no-gutters')
						v-col.ml-2(cols='auto')
							.btn-group
								v-btn(@click='onEditDeal' width='60' height='30' color='white' depressed tile dark)
									span 編輯
								v-btn.border-left(@click='onCopy' width='60' height='30' color='white' depressed tile dark)
									span 複製
								v-btn.border-left(@click='onDelete' width='60' height='30' color='white' depressed tile dark)
									span 刪除
						v-col.ml-2(cols='auto')
							.btn-group
								v-btn(@click='onChangeOwn' width='100' height='30' color='white' depressed tile dark)
									span 變更擁有者
						v-col.ml-2(v-if='!isProcessComplete' cols='auto')
							v-btn(@click='onWon()' width='60' height='32' color='green' depressed dark)
								span Won
							v-btn.ml-2(@click='onLost()' width='60' height='32' color='red' depressed dark)
								span Lost
						//- v-col.ml-2(v-else cols='auto')
						//-     v-btn(@click='onReStart()' width='100%' height='32' color='primary' depressed dark)
						//-         span 重啟商機
			#page-inner.pa-3.pt-0
				v-card.card-preface.card-shadow.px-0.py-6(flat)
					v-row
						v-col.py-0.px-2.px-md-4(cols='6' md='2')
							v-card-subtitle.pa-0.t-lightgary 帳戶名稱
							a.f-text-highlight.mb-0.t-primary(href='/Account' target='_blank') {{ deal.client }}
						v-col.py-0.px-2.px-md-4.border-left(cols='6' md='2')
							v-card-subtitle.pa-0.t-lightgary 結束日期
							p.f-text-highlight.mb-0 {{ deal.finishDate }}
						v-col.py-0.px-2.px-md-4.border-left(cols='6' md='2')
							v-card-subtitle.pa-0.t-lightgary 金額
							p.f-text-highlight.mb-0.t-green {{ deal.amount }}
						v-col.py-0.px-2.px-md-4.border-left(cols='6' md='2')
							v-card-subtitle.pa-0.t-lightgary 分數
							p.f-text-highlight.mb-0 {{ deal.fraction }}
						v-col.py-0.px-2.px-md-4.border-left(cols='6' md='2')
							v-card-subtitle.pa-0.t-lightgary 達成率
							p.f-text-highlight.mb-0 {{ deal.accomplish }}
						v-col.py-0.px-2.px-md-4.border-left(cols='12' md='2')
							v-list-item.pa-0(href='/Account' target='_blank')
								v-list-item-avatar.ma-0.mr-2(color='aliceBlue')
									span.mx-auto(v-if="deal.ownerPic === ''") {{ deal.ownerNickName }}
									img(v-else :src='deal.ownerPic' :alt='deal.ownerName')
								v-list-item-content.pa-0
									v-card-subtitle.pa-0.t-lightgary 機會擁有者
									p.mb-1.f-text-highlight {{ deal.ownerName }}
				v-card.process-card.mt-4(:class="stepContent ? '':'hide-process-info'")
					v-btn.process-box-open(v-if='!isProcessComplete' width='40' height='40' depressed fab outlined @click='switchStepContent()')
						v-icon(size='14' v-if='stepContent === true') icon-arrow-top
						v-icon(size='14' v-else) icon-arrow-btm
					v-stepper.process-box(v-model='processStep' :class="isProcessComplete ? 'complete':''")
						v-stepper-header.process-bar(v-if='isProcessLost' :class="isProcessLost ? 'lost':''")
							v-stepper-step
								span LOST
						v-stepper-header.process-bar(v-else :class="isProcessWon ? 'won':''")
							template(v-for='item in process')
								v-stepper-step(:key='`${item.step}-process`' :complete='processStep > item.step || isProcessWon' :step='item.step' :class='getProcessColor(item.step)' edit-icon='icon-check' :editable='isProcessEdit')
									span(v-if='processStep <= item.step || !isProcessWon')  {{ item.stepName }}
									span(v-else)  {{ item.workDay }} 天
						v-stepper-items(v-if='!isProcessComplete' :class="stepContent ? '':'hide' ")
							v-stepper-content.pa-0(v-for='(item, index) in process' :key='`${index}-process`' :step='index + 1')
								v-card(color flat height='146')
									v-row(no-gutters='no-gutters')
										v-col.pr-md-4(cols='12' md='6')
											.d-flex.align-center
												h4.my-2 關鍵字
												v-btn.ml-1(v-if='showEditKeywordBtn' icon color='gary' @click='editKeyword(item)')
													v-icon(size='20') icon-edit
												.d-flex.align-center.ml-auto(v-if='showEditKeywordCheckBtn')
													v-btn(width='70' text color='gary' @click='cancelEditKeyword(item, idx)')
														v-icon(size='20') icon-cancel
														span 取消
													v-btn.ml-3(width='70' text color='success' @click='confirmEditKeyword()')
														v-icon(size='20') icon-check
														span 儲存
											p.d-flex.align-center.input-has-label.dance
												label(:for='`${index}-finishDate`') 結束日期
												v-text-field(v-model='item.stepFinishDate' :id='`${index}-finishDate`' solo='solo' dense='dense' :readonly='isReadonlyKeyword')
											p.d-flex.align-center.input-has-label.dance
												label(:for='`${index}-describe`') 描述
												v-text-field(v-model='item.describe' :id='`${index}-describe`' solo='solo' dense='dense' :readonly='isReadonlyKeyword')
											p.d-flex.align-center.input-has-label.dance
												label(:for='`${index}-budgetConfirm`') Budget Confirm
												v-text-field(v-model='item.budgetConfirm' :id='`${index}-budgetConfirm`' solo='solo' dense='dense' :readonly='isReadonlyKeyword')
										v-col.pl-md-4(cols='12' md='6')
											h4.my-2  {{ item.guideTitle }}
											p.mb-0.pt-1  {{ item.guideContent }}
				v-alert.pinning-card.mt-3(v-model='pinning' type='warning' dense icon='mdi-firework')
					v-row(no-gutters)
						v-col(cols='10')
							strong.ml-3 商務發生嚴重問題 請勿電訪
							span.ml-3 2021/06/30 10:50  Ashiey
						v-col.text-right(cols='2')
							v-menu(bottom left)
								template(v-slot:activator='{ on, attrs }')
									v-btn.ml-3(color='white' dark v-bind='attrs' v-on='on' icon dense min-height='20' height='20' width='20')
										v-icon(size='16')  icon-dot-3
								v-list.pa-0
									v-list-item.px-0.text-center
										v-btn(@click='onPinning()' text='text')
											span.ml-1 關閉
				v-row(no-gutters='no-gutters' :class="stepContent ? 'deal-content mt-4':'deal-content big mt-4'")
					v-col(cols='12' md='8')
						v-card.deal-tab-card(flat)
							v-tabs.main-tabs(v-model='tab' background-color='transparent' :prev-icon="prevIcon ? 'mdi-arrow-left-bold-box-outline' : undefined" :next-icon="nextIcon ? 'mdi-arrow-right-bold-box-outline' : undefined" show-arrows)
								v-tabs-slider
								v-tab(v-for='item in tabs' :key='item.key' :href='`#tab-${item.id}`')
									| {{ item.name }}
								// - Detail -
								v-tab-item.pa-3(key='detail' value='tab-1' :transition='false' :reverse-transition='false')
									v-row(no-gutters)
										v-col.pr-md-4(cols='12' md='6')
											p.d-flex.align-center.input-has-label
												label(for='dealName') 機會名稱
												v-text-field#dealName(v-model='deal.dealName' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
											div.d-flex.align-center.justify-space-between.input-has-label
												.d-inline-flex.align-center
													label(for='client') 帳戶名稱
													v-text-field#client(v-if='!isDetailReadonly' v-model='deal.client' solo='solo')
													a.t-primary(v-else href='/Account') {{ deal.client }}
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
											p.d-flex.align-center.input-has-label
												label(for='dealType') 交易類型
												v-text-field#dealType(v-model='deal.dealType' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
											p.d-flex.align-center.input-has-label
												label(for='dealType') 交易金額
												v-text-field#amount(v-model='deal.amount' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
										v-col.pl-md-4(cols='12' md='6')
											p.d-flex.align-center.input-has-label
												label(for='dealType') 機會擁有者
												v-text-field#ownerName(v-model='deal.ownerName' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
											p.d-flex.align-center.input-has-label
												label(for='dealType') 結束日期
												v-text-field#finishDate(v-model='deal.finishDate' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
											p.d-flex.align-center.input-has-label
												label(for='dealType') 階段
												v-text-field#processNow(v-model='deal.processNow' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
											p.d-flex.align-center.input-has-label
												label(for='dealType') 可能性(%)
												v-text-field#possibility(v-model='deal.possibility' solo='solo' :readonly='isDetailReadonly')
												v-btn.ml-1(v-if='showDetailEditBtn' icon color='gary' @click='onEditDetail')
													v-icon(size='18') icon-edit
										v-col.d-flex.align-center.justify-end.ml-auto(cols='6' v-if='!showDetailEditBtn')
											v-btn(width='70' text color='gary' @click='cancelEditDetail()')
												v-icon(size='20') icon-cancel
												span 取消
											v-btn.ml-3(width='70' text color='success' @click='confirmEditDetail()')
												v-icon(size='20') icon-check
												span 儲存
								// - Time line -
								v-tab-item.pa-3(key='timeline' value='tab-2' :transition='false' :reverse-transition='false')
									v-tabs.borderBtn-tabs(height='32' background-color='white' :hide-slider='true' :centered='true')
										v-tab(v-for='item in timeLineType' :key='item.key' :href='`#tab-${item.id}`')
											| {{ item.name }}
										// - Time Line - All -
										v-tab-item.pa-3.pt-5(key='all' value='tab-1' :transition='false' :reverse-transition='false')
											v-timeline.pt-0(dense align-top)
												v-timeline-item(v-for='(item, index) in timeLine' :key='index' color='success' :icon='item.icon' small right fill-dot)
													// - Time Line Item - Meeting and Call  -
													v-card.px-3.py-1(v-if="item.type === 'call' || item.type === 'meeting'" flat outlined)
														v-row.justify-space-between.align-center(no-gutters)
															v-col(cols='12' md='5')
																v-card-subtitle.pa-0
																	| {{ item.title }}
															v-col.f-smaller.t-lightgary.t-lightgary.detail(cols='12' md='7')
																span.mr-3
																	| {{ item.date }} ・
																	| {{ item.owner }}
																.d-flex.align-center.mr-3
																	v-icon(size='17') icon-group
																	span  {{ item.client }}
																.d-flex.align-center
																	v-icon(size='15') icon-company
																	span  {{ item.clientConpany }}
																v-menu(bottom left)
																	template(v-slot:activator='{ on, attrs }')
																		v-btn.ml-3(color='primary' dark v-bind='attrs' v-on='on' icon)
																			v-icon(size='16')  icon-dot-3
																	v-list.pa-0
																		v-list-item.px-0.text-center
																			v-btn(@click='onEditAactivity(item)' text='text')
																				span.ml-1 編輯
																		v-list-item.px-0.text-center
																			v-btn(@click='onDelete()' text='text')
																				span.ml-1 刪除
													// - Time Line Item - stepChange and moneyChange -
													v-card.my-1.px-3.py-2(v-if="item.type === 'stepChange' || item.type === 'moneyChange'" flat outlined)
														v-row.justify-space-between.align-center(no-gutters)
															v-col(cols='12' md='6')
																v-card-subtitle.pa-0
																	span  {{ item.old }}
																	v-icon(size='16') icon-right-small
																	span  {{ item.new }}
															v-col.f-smaller.t-lightgary.t-lightgary.detail.d-flex.align-center(cols='12' md='6')
																span.mr-3
																	| {{ item.date }} ・
																	| {{ item.owner }}
													// Time Line Item - note
													v-card.timeLine-note.px-3.py-1(v-if="item.type === 'note'" flat outlined)
														v-row.justify-space-between.align-center(no-gutters)
															v-col.f-smaller.t-lightgary.t-lightgary.d-flex.align-center(cols='10')
																span.mr-3
																	| {{ item.date }} ・
																	| {{ item.owner }}
															v-col.text-right(cols='1')
																v-menu(bottom left)
																	template(v-slot:activator='{ on, attrs }')
																		v-btn.ml-3(color='primary' dark v-bind='attrs' v-on='on' icon)
																			v-icon(size='16')  icon-dot-3
																	v-list.pa-0
																		v-list-item.px-0.text-center
																			v-btn(text='text')
																				span.ml-1 編輯
																		v-list-item.px-0.text-center
																			v-btn(@click='onDetele(item)' text='text')
																				span.ml-1 刪除
															v-col(cols='12')
																p.mb-1  {{ item.content }}
													// - Time Line Item - Mail  -
													v-card.px-3.py-1(v-if="item.type === 'mail'" flat outlined)
														v-row.justify-space-between.align-center(no-gutters)
															v-col.d-flex.align-center(cols='10')
																v-card-subtitle.pa-0
																	| {{ item.title }}
															v-col.text-right(cols='1')
																v-menu(bottom left)
																	template(v-slot:activator='{ on, attrs }')
																		v-btn.ml-3(color='primary' dark v-bind='attrs' v-on='on' icon)
																			v-icon(size='16')  icon-dot-3
																	v-list.pa-0
																		v-list-item.px-0.text-center
																			v-btn(text='text')
																				span.ml-1 回信
																		v-list-item.px-0.text-center
																			v-btn(@click='onDetele(item)' text='text')
																				span.ml-1 刪除
															v-col.f-smaller.t-lightgary.d-flex.align-center(cols='12')
																span.mr-3
																	| {{ item.date }} ・
																	| {{ item.owner }}
															v-col(cols='12 mt-2')
																p.mb-1  {{ item.content }}
										// - Time Line - money change -
										v-tab-item.pa-3.pt-5(key='moneyChange' value='tab-2' :transition='false' :reverse-transition='false')
											v-timeline.pt-0(dense align-top)
												template(v-for='(item, index) in timeLine')
													v-timeline-item(v-if="item.type === 'moneyChange'" :key='index' color='success' :icon='item.icon' small right fill-dot)
														v-card.my-1.px-3.py-2(flat outlined)
															v-row.justify-space-between.align-center(no-gutters)
																v-col(cols='12' md='6')
																	v-card-subtitle.pa-0
																		span  {{ item.old }}
																		v-icon(size='16') icon-right-small
																		span  {{ item.new }}
																v-col.f-smaller.t-lightgary.t-lightgary.detail.d-flex.align-center(cols='12' md='6')
																	span.mr-3
																		| {{ item.date }} ・
																		| {{ item.owner }}
										// - Time Line - step change -
										v-tab-item.pa-3.pt-5(key='stepChange' value='tab-3' :transition='false' :reverse-transition='false')
											v-timeline.pt-0(dense align-top)
												template(v-for='(item, index) in timeLine')
													v-timeline-item(v-if="item.type === 'stepChange'" :key='index' color='success' :icon='item.icon' small right fill-dot)
														v-card.my-1.px-3.py-2(flat outlined)
															v-row.justify-space-between.align-center(no-gutters)
																v-col(cols='12' md='6')
																	v-card-subtitle.pa-0
																		span  {{ item.old }}
																		v-icon(size='16') icon-right-small
																		span  {{ item.new }}
																v-col.f-smaller.t-lightgary.t-lightgary.detail.d-flex.align-center(cols='12' md='6')
																	span.mr-3
																		| {{ item.date }} ・
																		| {{ item.owner }}
										// - Time Line - mail -
										v-tab-item.pa-3.pt-5(key='mail' value='tab-4' :transition='false' :reverse-transition='false')
											v-timeline.pt-0(dense align-top)
												template(v-for='(item, index) in timeLine')
													v-timeline-item(v-if="item.type === 'mail'" :key='index' color='success' :icon='item.icon' small right fill-dot)
														v-card.my-1.px-3.py-2(flat outlined)
															v-row.justify-space-between.align-center(no-gutters)
																v-col(cols='12' md='6')
																	v-card-subtitle.pa-0
																		span  {{ item.title }}
																v-col.f-smaller.t-lightgary.t-lightgary.detail.d-flex.align-center(cols='12' md='6')
																	span.mr-3
																		| {{ item.date }} ・
																		| {{ item.owner }}
										// - Time Line - note -
										v-tab-item.pa-3.pt-5(key='note' value='tab-5' :transition='false' :reverse-transition='false')
											v-timeline.pt-0(dense align-top)
												template(v-for='(item, index) in timeLine')
													v-timeline-item(v-if="item.type === 'note'" :key='index' color='success' :icon='item.icon' small right fill-dot)
														v-card.timeLine-note.px-3.py-1(flat outlined)
															v-row.justify-space-between.align-center(no-gutters)
																v-col.f-smaller.t-lightgary.t-lightgary.d-flex.align-center(cols='10')
																	span.mr-3
																		| {{ item.date }} ・
																		| {{ item.owner }}
																v-col.text-right(cols='1')
																	v-menu(bottom left)
																		template(v-slot:activator='{ on, attrs }')
																			v-btn.ml-3(color='primary' dark v-bind='attrs' v-on='on' icon)
																				v-icon(size='16')  icon-dot-3
																		v-list.pa-0
																			v-list-item.px-0.text-center
																				v-btn(text='text')
																					span.ml-1 編輯
																			v-list-item.px-0.text-center
																				v-btn(@click='onDetele(item)' text='text')
																					span.ml-1 刪除
																v-col(cols='12')
																	p.mb-1  {{ item.content }}
										// - Time Line - file -
										v-tab-item.pa-3.pt-5(key='file' value='tab-6' :transition='false' :reverse-transition='false')
											v-timeline.pt-0(dense align-top)
												template(v-for='(item, index) in timeLine')
													v-timeline-item(v-if="item.type === 'file'" :key='index' color='success' :icon='item.icon' small right fill-dot)
														v-card.px-3.py-1(flat outlined)
															v-row.justify-space-between.align-center(no-gutters)
																v-col(cols='12' md='6')
																	v-card-subtitle.pa-0
																		| {{ item.fileName }}
																v-col.f-smaller.t-lightgary.t-lightgary.detail.d-flex.align-center(cols='12' md='6')
																	span  {{ item.date }} ・ {{ item.owner }}
																	v-menu(bottom left)
																		template(v-slot:activator='{ on, attrs }')
																			v-btn.ml-3(color='primary' dark v-bind='attrs' v-on='on' icon)
																				v-icon(size='16')  icon-dot-3
																		v-list.pa-0
																			v-list-item.px-0.text-center
																				v-btn(text='text')
																					span.ml-1 下載
																			v-list-item.px-0.text-center
																				v-btn(@click='onDetele(item)' text='text')
																					span.ml-1 刪除
								// - note -
								v-tab-item.pa-3(key='note' value='tab-3' :transition='false' :reverse-transition='false')
									.editor
									v-timeline.mt-5.pt-0(dense align-top)
