					break;
				case 5:
					color = 'red';
					break;
				default:
					color = 'gary';
					break;
			}
			return color;
		},
		switchStepContent() {
			if (this.stepContent === true) {
				this.stepContent = false;
			} else {
				this.stepContent = true;
			}
		},
		onWon() {
			this.stepContent = false;
			this.isProcessEdit = false;
			this.isProcessComplete = true;
			this.isProcessWon = true;
		},
		onLost() {
			this.stepContent = false;
			this.isProcessEdit = false;
			this.isProcessComplete = true;
			this.isProcessLost = true;
		},
		onReStart() {
			this.stepContent = true;
			this.isProcessComplete = false;
			this.isProcessLost = false;
			this.isProcessWon = false;
		},
		// edit Keyword
		editKeyword: function(data) {
			this.isReadonlyKeyword = false;
			this.showEditKeywordBtn = false;
			this.showEditKeywordCheckBtn = true;
			this.keywordOriginalData = data;
		},
		cancelEditKeyword() {
			this.isReadonlyKeyword = true;
			this.showEditKeywordBtn = true;
			this.showEditKeywordCheckBtn = false;
		},
		confirmEditKeyword() {
			this.isReadonlyKeyword = true;
			this.showEditKeywordBtn = true;
			this.showEditKeywordCheckBtn = false;
		},
		onEditDetail() {
			this.showDetailEditBtn = false;
			this.isDetailReadonly = false;
		},
		cancelEditDetail() {
			this.showDetailEditBtn = true;
			this.isDetailReadonly = true;
		},
		confirmEditDetail() {
			this.showDetailEditBtn = true;
			this.isDetailReadonly = true;
		},
		// Common Function
		onCall: function (cell) {
			return 'tel:'+cell;
		},
		onMall: function (mail) {
			return 'mailto:'+mail;
		},
		// Dialog
		onEditDeal() {
			this.editDealDialog = true;
		},
		onEmitEditDealDialog(val) {
			this.editDealDialog = false;
			if (val) {
				this.showSussessDialog();
			}
		},
		onChangeOwn() {
			this.changeOwnDialog = true;
		},
		onEmitChangeOwnDialog(val) {
			this.changeOwnDialog = false;
			if (val) {
				this.showSussessDialog();
			}
		},
		onMainContacts() {
			this.mainContactsDialog = true;
		},
		onEmitMainContactsDialog() {
			this.mainContactsDialog = false;
		},
		onEditAactivity(data) {
			console.log(data);
			this.activityDialog = true;
		},
		onEmitActivityDialog(val) {
			this.activityDialog = false;
			if (val) {
				this.showSussessDialog();
			}
		},
		showSussessDialog() {
			this.successDialog = true;
		},
		onEmitSuccessDialog() {
			this.successDialog = false;
		},
		showMessageDialog(message) {
			this.messageDialog = true;
			this.message = message;
		},
		onEmitMessageDialog(val) {
			this.messageDialog = false;
			if (val) { this.successDialog = true; }
		},
		showErrorDialog(message) {
			this.errorDialog = true;
			this.errorMessage = message;
		},
		onEmitErrorDialog(val) {
			this.errorMessage = false;
			if (val) { console.log('如果確認 執行'); }
		},
		onDelete () {
			this.showMessageDialog('您確定要刪除此工作嗎？');
		},
		onCopy() {
			this.showMessageDialog('您確定要複製此商機嗎？');
		},
		// Dialog Area ----------------------------------------- [End]
		onPinning() {
			this.pinning = false;
		},
		// chip remove
		addresseeRemove (item) {
			const index = this.addressee.indexOf(item.name)
			if (index >= 0) this.addressee.splice(index, 1)
		},
		copyRemove (item) {
			const index = this.copy.indexOf(item.name)
			if (index >= 0) this.copy.splice(index, 1)
		},
		// email Template Area ----------------------------------------- [Start]
		selectTemplate(v) {
			if (v) {
				const selectTemplate = _.filter(this.emailTemplateData, { 'idx': v });
				this.mainEditor = selectTemplate[0].templateContent;
				this.emailTemplateContactValList = selectTemplate[0].contactTemplateVal;
				this.emailTemplateDealValList = selectTemplate[0].dealTemplateVal
			} else {
				this.mainEditor = '';
				this.emailTemplateContactValList = '';
				this.emailTemplateDealValList = '';
			}
		},
		onEditorBlur(quill) {
			console.log("editor blur!", quill);
		},

		onEditorFocus(quill) {
			console.log("editor focus!", quill);
		},

		onSelectionChange(range) {
			console.log("selection change!", range);
		},
		onEmailTemplateValChange(val, insertText) {
			if (val && insertText) {
				this.mainEditor = this.mainEditor.replace(val, insertText);
			}
		}
		// email Template Area ----------------------------------------- [End]
	}
	}
</script>
